  margin: 0;
  padding: 0;
}

h1 {
  font: 700 4rem "Roboto", sans-serif;
  color: rgb(255, 255, 255);
}

h2 {
  font: 700 2.5rem "Roboto", sans-serif;
  color: rgb(255, 255, 255);
}

h3 {
  font: 1rem "Roboto", sans-serif;
  color: rgb(255, 255, 255);
}

a {
  text-decoration: none;
  font: 1rem "Roboto", sans-serif;
  color: rgb(255, 255, 255);
}
a:hover {
  transition: 0.4s;
  color: rgba(255, 255, 255, 0.8);
}

img {
  max-width: 100%;
  height: auto;
}

input {
  border: none;
  border-radius: 3.125rem;
  background: none;
}

input:active, :hover, :focus {
  outline: 0;
  outline-offset: 0;
}

main {
  width: 100%;
  height: auto;
  overflow-x: hidden;
  background: url("/img/bgc.jpg") no-repeat center center;
  background-size: cover;
  background-attachment: fixed;
}

.about__us-wrap, .quick__start-wrap, .ability__content-wrap, .partners__content-wrap, .system__content-wrap, .col__content-wrap, .footer-wrap {
  width: 100%;
  height: auto;
  padding: 7.5rem 0;
}

.about__us-wrap {
  padding: 10rem 0;
}

.footer-wrap {
  padding: 0rem 0 3rem 0;
}

.partners__content-inner, .about__us-inner, .ability__content-inner, .quick__start-inner, .system__content-inner, .col__content-inner, .footer__inner {
  width: 80%;
  height: 100%;
  margin: 0 auto;
}

.caption {
  width: auto;
  height: auto;
}
.caption .title {
  display: flex;
  align-items: center;
  gap: 2.5rem;
}

/*========== ProgressBar ==========*/
::-webkit-scrollbar {
  width: 0.25rem;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(90deg, rgb(200, 154, 252), rgb(124, 106, 250));
  border-radius: 0.25rem;
}

/*========== Preloader ==========*/
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgb(0, 0, 0);
}

.hide-preloader {
  animation: hide-preloader 1s;
}

.preloader-hidden {
  display: none;
}

@keyframes hide-preloader {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.scrollup {
  position: fixed;
  right: 1rem;
  bottom: -20%;
  display: flex;
  background-color: rgb(18, 18, 18);
  border-radius: 0.25rem;
  padding: 0.45rem;
  opacity: 0.9;
  z-index: 99;
  transition: 0.4s;
}
.scrollup:hover {
  background-color: rgba(78, 78, 78, 0.75);
  opacity: 1;
}

.scrollup__icon {
  color: white;
  font-size: 1.35rem;
}

/*      show scroll up      */
.show-scroll {
  bottom: 5rem !important;
}